import React, { Component, memo } from 'react';
import { WEBSITE_EMBED_URL } from '../../../../../actions/api';

class Theme extends Component {
    componentDidMount() {
        this.embedCode();
    }

    embedCode = () => {
        var isTagEmbd = !1, isTagShop = !0;
        window.isTagShop = true;
        window.preview = true;
        var AppPath = `${WEBSITE_EMBED_URL}${localStorage.getItem('ownerId') == "139294" ? 'khaadi/' : ''}embed.json`;
        fetch(AppPath)
            .then(response => response.json())
            .then(json => {
                const { css, id, script } = json;
                let myCss = document.createElement("link");
                myCss.setAttribute("type", 'text/css');
                myCss.setAttribute("rel", 'stylesheet');
                myCss.setAttribute("href", css);
                document.head.appendChild(myCss);
                let myreact = document.createElement("script");
                myreact.setAttribute("src", script);
                document.body.appendChild(myreact);
            });
    };


    componentWillUnmount() {
        this.onLoadRemoveScript()
        this.onLoadRemoveCss()
    }

    onLoadRemoveCss = () => {
        try {
            const scriptList = document.querySelectorAll("link[type='text/css']")
            const convertedNodeList = Array.from(scriptList)
            const testScript = convertedNodeList.find(script => script.id === "preview_embed_css")
            testScript.parentNode.removeChild(testScript)
        }
        catch (ex) {

        }
    }
    onLoadRemoveScript = () => {
        try {
            const scriptList = document.querySelectorAll("script[type='text/javascript']")
            const convertedNodeList = Array.from(scriptList)
            const testScript = convertedNodeList.find(script => script.id === "preview_embed_js")
            testScript.parentNode.removeChild(testScript)
        }
        catch (ex) {

        }
    }

    render() {
        const rootID = `root_taggShopRoot`;
        const {webId} = this.props
        return (
            <>
                <div className="tagshop">
                    <div className="tagshop-socialwall" data-wall-id={webId} data-source="website" data-is-load="0" data-render-id={rootID} id={`co_${rootID}`}>
                        <div id={rootID}></div>
                    </div>
                </div>
            </>
        );
    }
}

export default memo(Theme);